@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

$primary-color: #ff3c78;
$light-black: rgba(0, 0, 0, 0.89);
$black: #000;
$white: #fff;
$grey: #aaa;


.contact-us{
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  body {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Poppins", sans-serif;
  }
  
  .container {
    max-width: 1200px;
    width: 90%;
    margin: 0 auto;
  }
  
  .contact {
    margin-top: 45px;
  }
  
  .form {
    display: flex;
    justify-content: space-between;
    margin: 80px 0;
  }
  
  .form .form-txt {
    flex-basis: 48%;
  }
  
  .form .form-txt h1 {
    font-weight: 600;
    color: $black;
    font-size: 40px;
    letter-spacing: 1.5px;
    margin-bottom: 10px;
    color: $light-black;
  }
  
  .form .form-txt span {
    color: $light-black;
    font-size: 14px;
  }
  
  .form .form-txt h3 {
    font-size: 22px;
    font-weight: 600;
    margin: 15px 0;
    color: $light-black;
  }
  
  .form .form-txt p {
    color: $light-black;
    font-size: 14px;
  }
  
  .form .form-details {
    flex-basis: 48%;
  }
  
  .form .form-details input[type="text"],
  .form .form-details input[type="email"] {
    padding: 15px 20px;
    color: $black;
    outline: none;
    border: 1px solid $grey;
    margin: 35px 15px;
    font-size: 14px;
  }
  
  .form .form-details textarea {
    padding: 15px 20px;
    margin: 0 15px;
    color: $black;
    outline: none;
    border: 1px solid $grey;
    font-size: 14px;
    resize: none;
  }
  
  .form .form-details button {
    padding: 15px 25px;
    color: $white;
    font-weight: 500;
    background: $black;
    outline: none;
    border: none;
    margin: 15px;
    font-size: 14px;
    letter-spacing: 2px;
    cursor: pointer;
  }
  
  @media (max-width: 500px) {
    .form {
      display: flex;
      flex-direction: column;
    }
    .form .form-details button {
      margin-left: 0;
    }
    .form .form-details input[type="text"],
    .form .form-details input[type="email"],
    .form .form-details textarea {
      width: 100%;
      margin-left: 0;
    }
    .form .form-details input[type="text"] {
      margin-bottom: 0px;
    }
  }
  
  @media (min-width: 501px) and (max-width: 768px) {
    .form {
      display: flex;
      flex-direction: column;
    }
    .form .form-details button {
      margin-left: 0;
    }
    .form .form-details input[type="text"],
    .form .form-details input[type="email"],
    .form .form-details textarea {
      width: 100%;
      margin-left: 0;
    }
    .form .form-details input[type="text"] {
      margin-bottom: 0px;
    }
  }
  
}
